import axios from "axios";

const ENDPOINT_PATH = "/api/";

export default {
    register(email, password, amount) {
        const params = { email, password, amount };
        return axios.post(ENDPOINT_PATH + "regiser", params);
    },
    login(email, password) {
        const params = { email, password };
        return axios.post(ENDPOINT_PATH + "login", params);
    },
    logout(user_id) {
        const params = { user_id };
        return axios.post(ENDPOINT_PATH + "logout", params);
    }
};