<template>
  <div class="inner-block">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-12">
          <h1 class="text-center">Login Page</h1>
          <form class="mt-4" @submit.prevent="login">
            <div class="form-group fixed-form-margin">
              <label for="email">Email:</label>
              <input v-model="email" class="form-control" type="email" id="email" required placeholder="user@mail.com" />
            </div>
            <div class="form-group fixed-form-margin">
              <label for="password">Password:</label>
              <input v-model="password" class="form-control" type="password" id="password" placeholder="Password" />
            </div>
            <p v-if="error" class="text-danger">
              Has introducido mal el email o la contrase単a.
            </p>
            <button class="btn btn-outline-primary" type="submit">Login</button>
          </form>
          <p class="mt-3">
            多Don't have an account? <router-link to="/signup">Signup</router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import auth from "@/assets/js/auth";
export default {
  data: () => ({
    email: "",
    password: "",
    error: false,
  }),
  methods: {
    async login() {
      try {
        await auth.login(this.email, this.password);
        this.$router.push("/");
      } catch (error) {
        this.error = true;
      }
    },
  },
};
</script>